.object-item-btn { display: flex; align-items: center; justify-content: center; padding: 8px; background-color: #f3f3f3; min-width: 80px; height: 40px; flex: 1 0 30%; margin: 1px; }

.object-item-btn__text { display: block; margin-left: 2px; overflow: hidden; text-overflow: ellipsis; white-space: nowrap; }

.object-item-btn__icon { color: #a7a7aa; width: 24px; height: 24px; display: flex; align-items: center; justify-content: center; }

.object-item-btn__icon .icon { display: block; }

.object-item-btn.is-active .object-item-btn__icon_blue { color: #2471be; }

.object-item-btn.is-active .object-item-btn__icon_green { color: #2eb525; }

.object-page { padding-bottom: 24px; }

.object-page__map { height: 200px; border-bottom: 1px solid #dfdfdf; }

.object-page__content { padding-top: 16px; }

.object-page__date, .object-page__location { font-size: 12px; }

.object-page__location { margin-top: 5px; }

.object-page__btns { display: flex; flex-wrap: wrap; align-items: center; margin: 16px -1px 0; }

.object-page__actions { display: flex; flex-wrap: wrap; margin: 16px -5px 0; align-items: center; }

.object-page__actions-btn { width: 50%; padding: 0 5px; }

.geozones-item { padding: 16px 0; border-bottom: 1px solid #dfdfdf; }

.geozones-item__top { display: flex; align-items: center; flex-wrap: wrap; }

.geozones-item__icon { margin-right: 16px; flex-shrink: 0; color: #a7a7aa; font-size: 16px; }

.geozones-item__icon .icon { display: block; max-width: 100%; }

.geozones-item__info { flex: 1 0 auto; max-width: calc(100% - 29px); }

.geozones-item__title { font-weight: 600; }

.geozones-item__details { display: flex; align-items: center; justify-content: space-between; }

.geozones-item__location { font-size: 12px; color: rgba(68, 68, 68, 0.54); overflow: hidden; text-overflow: ellipsis; white-space: nowrap; }

.geozones-item__location span { white-space: nowrap; }

.geozones-item__bottom { margin: 11px -1px 0; display: flex; align-items: center; flex-wrap: wrap; }

.geozones-item-btn { display: flex; align-items: center; justify-content: center; padding: 8px; background-color: #f3f3f3; height: 40px; flex: 1 0 calc(50% - 1px); }

.geozones-item-btn:not(:first-child) { margin: 1px; }

.geozones-item-btn__text { display: block; margin-left: 2px; overflow: hidden; text-overflow: ellipsis; white-space: nowrap; }

.geozones-item-btn__icon { color: #a7a7aa; width: 24px; height: 24px; display: flex; align-items: center; justify-content: center; }

.geozones-item-btn__icon .icon { display: block; }

.geozones-item-btn__icon_crossed { position: relative; }

.geozones-item-btn__icon_crossed:before, .geozones-item-btn__icon_crossed:after { content: ''; position: absolute; height: 22px; left: 50%; top: 50%; transform: translate(-50%, -50%) rotate(-45deg); }

.geozones-item-btn__icon_crossed:before { width: 2px; background-color: #f90e0e; z-index: 5; }

.geozones-item-btn__icon_crossed:after { width: 5px; background-color: #f3f3f3; }

.object-item-btn.is-active .geozones-item-btn__icon_blue { color: #2471be; }

.object-item-btn.is-active .geozones-item-btn__icon_green { color: #2eb525; }

.geozone-type { height: 100%; }

.geozone-type__inner { height: 100%; display: flex; flex-direction: column; }

.geozone-type__title { flex: 0 0 auto; padding-top: 24px; }

.geozone-type__wrap { padding: 24px 0; flex: 1 0 auto; display: flex; flex-wrap: wrap; }

.geozone-type__item { width: 100%; min-height: 90px; display: flex; align-items: center; justify-content: center; }

.geozone-type__item:not(:first-child) { margin-top: 8px; }

.geozone-type__item-content { display: flex; align-items: center; justify-content: center; flex-direction: column; width: 100%; height: 100%; border: 1px solid #dfdfdf; }

.geozone-type__input { display: none; }

.geozone-type__icon { width: 24px; height: 24px; color: #a7a7aa; }

.geozone-type__icon .icon { display: block; font-size: 24px; }

.geozone-type__name { font-size: 13px; margin-top: 10px; }

.drop-selection { padding: 24px 0; }

.drop-selection.is-open { opacity: 1; visibility: visible; }

.drop-selection__input { display: none; }

.drop-selection__item { display: flex; position: relative; align-items: center; padding-right: 15px; cursor: pointer; }

.drop-selection__item:not(:first-child) { margin-top: 24px; }

.drop-selection__icon { flex: 0 0 auto; }

.drop-selection__text { flex: 1 0 auto; margin-left: 10px; }

.drop-selection__check { position: absolute; right: 0; top: 50%; transform: translateY(-50%); color: #2471be; opacity: 0; visibility: hidden; transition: opacity .3s, visibility .3s; }

.drop-selection__input:checked ~ .drop-selection__check { opacity: 1; visibility: visible; }

.head-drop { z-index: 10; position: absolute; left: 0; top: 100%; width: 100%; width: 100%; display: flex; flex-direction: column; max-height: 50vh; border-top: 1px solid #dfdfdf; background-color: #fff; box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.25); opacity: 0; visibility: hidden; transition: opacity .3s, visibility .3s; }

.head-drop_fullPage { height: calc(100vh - 56px - 54px); max-height: none; }

.head-drop_noborder { border: none; }

.head-drop.is-open { opacity: 1; visibility: visible; }

.head-drop__content { position: relative; flex: 1 0 auto; }

.head-drop__content-wrap { position: absolute; left: 0; top: 0; width: 100%; height: 100%; padding: 24px 0 0; overflow: auto; }

.head-drop__footer { flex: 0 0 auto; }

[data-modal] { position: fixed; z-index: -1; top: 0; left: 0; right: 0; bottom: 0; opacity: 0; visibility: hidden; min-width: 320px; text-align: center; font-size: 0; transition: opacity 0.3s, visibility 0.3s, z-index 0.3s; }

[data-modal]:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }

[data-modal].is-open { opacity: 1; visibility: visible; z-index: 100; }

[data-modal-container] { position: relative; z-index: 1; font-size: 13px; vertical-align: bottom; display: inline-block; }

.modal { overflow-x: hidden; color: #444; background-color: rgba(0, 0, 0, 0.6); }

.modal__container { padding: 16px 0 24px; text-align: left; width: 100%; background-color: #fff; }

.modal__text { margin-top: 16px; }

.modal__title, .modal__text { max-width: 280px; }

.modal__row { display: flex; flex-wrap: wrap; margin: 24px -4px 0; }

.modal__col { flex: 0 0 50%; max-width: 50%; padding: 0 4px; }

.zone-page__map { height: 255px; border: 1px solid #dfdfdf; }

.zone-page__content { padding: 16px 0 24px; }

.zone-page__btns { margin: 0 -1px; display: flex; align-items: center; flex-wrap: wrap; }

.zone-page__items { margin-top: 2px; }

.zone-page__bottom { margin-top: 24px; }

.zone-page__description { margin-top: 8px; color: rgba(68, 68, 68, 0.54); }

.zone-page__link { display: block; margin-top: 8px; color: #2471be; text-decoration: underline; }
