
[data-modal] {
    position: fixed;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0;
    visibility: hidden;
    min-width: 320px;
    // padding: 0 40px
    @include vertical;
    // +clr
    // +gpu
    // overflow: auto
    transition: opacity 0.3s, visibility 0.3s, z-index 0.3s;
    &.is-open {
        opacity: 1;
        visibility: visible;
        z-index: 100; } }

[data-modal-container] {
    position: relative;
    z-index: 1;
    font-size: 13px;
    // float: right
    vertical-align: bottom;
    display: inline-block; }
